import { useEffect, useState } from 'react';
import './styles/snackbar.css'

const Snackbar = props => {

    useEffect(() => {
        if (props.shown) {
            setTimeout(() => props.onClose(), 3000)
        }
    }, [props])

    return (
        <>
            <div className={`snackbar ${props.shown && 'show'}`}>
                {props.children}
            </div>
        </>
    )
}
  
export default Snackbar;