const init = {
    activeModal: null,
    cart: [],
    price: 0,
    menu: [{
        "title": "Загрузка...",
        "products": [
          
        ]
    }],
    deliveryData: {
        "phone": "+7",
        "address": ""
    },
    addressSuggestions: [],
    selectedProduct: {
        id: 0,
        structure: [],
        nutritional: []
    },
    requestError: null
};

export const homeReducer = (state = init, action) => {
    switch (action.type) {
        case "updateModal":
            return {
                ...state, activeModal: action.payload
            };
        case "updateCart":
            return {
                ...state, cart: action.payload
            };
        case "updateMenu":
            return {
                ...state, menu: action.payload
            };
        case "updatePrice":
            return {
                ...state, price: action.payload
            };
        case "updateDeliveryData":
            return {
                ...state, deliveryData: action.payload
            };
        case "updateAddressSuggestions":
            return {
                ...state, addressSuggestions: action.payload
            };
        case "updateSelectedProduct":
            return {
                ...state, selectedProduct: action.payload
            };
        case "updateRequestError":
            return {
                ...state, requestError: action.payload
            };
        default:
            return state;
    }
};