import { useEffect, useState } from 'react';
import arrow from '../assets/arrow.svg'

import './styles/scrolltop.css'

const ScrollTop = props => {
    const [scrollPosition, setScrollPosition] = useState(0);
    const handleScroll = () => {
        const position = window.pageYOffset;
        setScrollPosition(position);
    };
    useEffect(() => {
        window.addEventListener('scroll', handleScroll, { passive: true });
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <>
            {scrollPosition > 100 ? <div className='scrollTopButton' onClick={() => window.scrollTo(0, 0)}>
            <svg width="12" height="11" viewBox="0 0 9 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M3.69887 7.28015C3.65779 7.24013 3.48213 7.08901 3.33763 6.94824C2.42883 6.12294 0.941333 3.96999 0.487292 2.84315C0.414333 2.67201 0.259917 2.23936 0.25 2.00819C0.25 1.78668 0.301 1.57553 0.404417 1.37404C0.548917 1.12286 0.776292 0.921365 1.04475 0.810958C1.23104 0.739883 1.7885 0.629475 1.79842 0.629475C2.40829 0.519068 3.39925 0.458344 4.49433 0.458344C5.53771 0.458344 6.48829 0.519068 7.10737 0.609464C7.11729 0.619815 7.81004 0.730222 8.04733 0.85098C8.48083 1.07249 8.75 1.50514 8.75 1.96817V2.00819C8.73938 2.30974 8.47021 2.94389 8.46029 2.94389C8.00554 4.01002 6.591 6.11328 5.65104 6.95859C5.65104 6.95859 5.4095 7.19665 5.25863 7.30016C5.04187 7.46163 4.77342 7.54168 4.50496 7.54168C4.20533 7.54168 3.92625 7.45128 3.69887 7.28015Z" fill={getComputedStyle(document.body).getPropertyValue('--dropdown_select_text')} fill-opacity="0.44"/>
            </svg>

            </div> : <></>}
        </>
    )
}
  
export default ScrollTop;