import { useEffect, useState } from 'react';

import plus from '../assets/plus.svg'
import minus from '../assets/minus.svg'

import './styles/product.css'

const Product = props => {
    const [product, setProduct] = useState({
        ...props.info,
        c: props.category
    })
    const [inCart, setInCart] = useState(0)
    const isInCart = props.cart.find(item => product.id === item.id);

    return (
        <>
            <div className='product' key={product.id}>
            <img src={product.img} className='image' onClick={() => props.openInfo(product)} />
            <div className='title' onClick={() => props.openInfo(product)}>{product.title}</div>
            <div className='description' onClick={() => props.openInfo(product)}>{product.price}{product.currency} · {product.info}</div>
            {!isInCart ? <div className='button' onClick={() => {
                props.addToCart(product)
            }}>Добавить</div> : (
                <div className='cart'>
                    <div className='unadd' onClick={() => props.deleteFromCart(product)}><img src={minus} /></div>
                    <div className='count'>{isInCart.count}</div>
                    <div className='add'><img onClick={() => props.addToCart(product)} src={plus} /></div>
                </div>
            )}
        </div>
        </>
    )
}

const Group = props => {

    const openInfo = (product) => {
        props.setSelectedProduct(product)
        props.setActiveModal('productInfo')
    }

    return (
      <div className="group" key={props.cid}>
        <div className='title'>{props.title}</div>

        <div className='products'>
            {props.items.map((product, i) => {
                return (
                    <Product openInfo={openInfo} category={props.cid} location={props.location} deleteFromCart={props.deleteFromCart} info={product} addToCart={props.addToCart} cart={props.cart} />
                )
            })}
        </div>
      </div>
    );
}
  
export default Group;