import { useEffect, useState } from 'react';
import './styles/dropdown.css'

let top = {
    1: 274,
    2: 314,
    3: 354,
    4: 394,
    5: 434,
    6: 474,
    7: 504,
    8: 544,
    9: 584,
    10: 624,
    11: 664
}

const Dropdown = props => {
    const [pos, setPos] = useState({
        x: 0,
        y: 0
    })

    /* 354
    1 - 274px 80
    2 - 314px 40
    3 - 354px 0
    4 - 394px -40 */
    useEffect(() => {
        setPos(props.refElement.current.getBoundingClientRect())
        console.log(props.count)
    }, [])

    return (
        <>
           <div class='dropdown-wrapper' style={{ transform: `translate(24px, calc(${top[props.count]}px))`, display: !props.shown ? 'none' : 'block' }}>
              <div class='dropdown'>
                <div class='dropdown-content' style={{ width: props.dropdownWidth }}>
                    {props.children}
                </div>
              </div>
            </div>
        </>
    )
}
  
export default Dropdown;