import React, { useEffect, useState, useRef } from 'react';
import { motion } from 'framer-motion'
import { useSelector, useDispatch } from "react-redux";

import Select from '../components/Select';
import Dropdown from '../components/Dropdown';
import ProductsGroup from '../components/ProductsGroup'
import Modal from '../components/Modal';
import Input from '../components/Input';

import location from '../assets/location.svg'
import arrow from '../assets/arrow.svg'
import close from '../assets/close.svg'
import plus from '../assets/plus.svg'
import promo from '../assets/promo.svg'
import minus from '../assets/minus.svg'

import API from '../api';
import Snackbar from '../components/Snackbar';
import ScrollTop from '../components/ScrollTop';

if (window.location.href.split('?')[1]) {
  let a1 = window.location.href.split('?')[1].split('#')
  let a2 = a1[0].split('&')
  let a3 = a2[0].split('=')
  let a4 = a2[1].split('=')
      
  if (a3[1] && a4[1]) {
    window.lid = a4[1]
    window.rid = a3[1]
  }
}

const HomeView = props => {
  const dispatch = useDispatch();
  const home = useSelector((s) => s.home);
  
  const dropdown = useRef(null)

  const [shown, setShown] = React.useState(false);
  const [, updateState] = React.useState();
  const forceUpdate = React.useCallback(() => updateState({}), []);

  useEffect(() => {
    const timeOutId = setTimeout(() => {
      const api = new API({
        id: Number(window.rid),
        location: Number(window.lid)
      })
      async function fetchData() {
        let s = await api.suggestAddress(home.deliveryData.address)
        dispatch({
          type: "updateAddressSuggestions",
          payload: s.response
        })
      }
      if (!shown) return
      if (home.deliveryData.address.length > 2) {
          fetchData()
      }
    }, 1000)
    return () => clearTimeout(timeOutId);
  }, [home.deliveryData.address]);

  useEffect(() => {
    window.Telegram.WebApp.MainButton.setParams({
      text_color: '#fff'
    }).onClick(mainBtnClicked);
    window.Telegram.WebApp.expand()

    async function fetchData() {
      const api = new API({
        id: Number(window.rid),
        location: Number(window.lid)
      })
      let pr = await api.getInfo()
      if (pr.result !== null) {
        dispatch({
          type: "updateMenu",
          payload: pr.result.products
        })
      }
    }
    fetchData()
  }, [])

  useEffect(() => {
    if (home.cart.length < 1) {
      window.Telegram.WebApp.MainButton.setParams({
        is_visible: false,
        text: `Оплатить · 0₽`,
        color: '#40C62B'
      }).hideProgress();
    }
  }, [home.cart])
  
  const mainBtnClicked = (data) => {
    dispatch({
      type: "updateModal",
      payload: 'order'
    })
    var mainButton = window.Telegram.WebApp.MainButton;
    mainButton.setParams({
      is_visible: false,
      text: `Оплатить · ${home.price}₽`,
      color: '#40C62B'
    }).hideProgress();
  }

  const addToCart = (data) => {
    let inCart = false
    let cartId = 0
    var mainButton = window.Telegram.WebApp.MainButton;
    for (let i in home.cart) {
      if (home.cart[i].id == data.id) {
        inCart = true
        cartId = i
      }
    }

    if (!inCart) {
      dispatch({
        type: "updateCart",
        payload: [...home.cart, { id: data.id, category: data.c, data, count: 1 }]
      })
      dispatch({
        type: "updatePrice",
        payload: home.price + Number(data.price)
      })
      forceUpdate()
      mainButton.setParams({
        is_visible: true,
        text: `Оплатить · ${home.price + Number(data.price)}₽`,
        color: '#40C62B'
      }).hideProgress();
    } else {
      let _cart = home.cart
      _cart[cartId].count += 1
      dispatch({
        type: "updateCart",
        payload: _cart
      })
      dispatch({
        type: "updatePrice",
        payload: 0
      })
      let _price = 0
      home.cart.forEach(element => _price = _price + (element.data.price * element.count));
      dispatch({
        type: "updatePrice",
        payload: _price
      })
      forceUpdate()
      mainButton.setParams({
        is_visible: true,
        text: `Оплатить · ${_price}₽`,
        color: '#40C62B'
      }).hideProgress();
    }
  };
  
  const deleteFromCart = (data) => {
    let inCart = false
    var mainButton = window.Telegram.WebApp.MainButton;
    let cartId = 0

    for (let i in home.cart) {
      if (home.cart[i].id == data.id) {
        inCart = true
        cartId = i
      }
    }

    console.log(cartId)

    if (inCart) {
      if (home.cart[cartId].count == 1) {
        dispatch({
          type: "updateCart",
          payload: home.cart.filter(item => item.data.id !== data.id)
        })
        dispatch({
          type: "updatePrice",
          payload: home.price - Number(data.price)
        })
        forceUpdate()
        if ((home.price - Number(data.price)) > 0) {
          mainButton.setParams({
            is_visible: true,
            text: `Оплатить · ${home.price - Number(data.price)}₽`,
            color: '#40C62B'
          }).hideProgress();
        } else {
          mainButton.setParams({
            is_visible: false,
            text: `Оплатить · ${home.price - Number(data.price)}₽`,
            color: '#40C62B'
          }).hideProgress();
        }
      } else {
        let _cart = home.cart
        _cart[cartId].count -= 1
        dispatch({
          type: "updateCart",
          payload: _cart
        })
        dispatch({
          type: "updatePrice",
          payload: home.price - Number(data.price)
        })
        let _price = 0
        home.cart.forEach(element => _price = _price + (element.data.price * element.count));
        dispatch({
          type: "updatePrice",
          payload: _price
        })
        forceUpdate()
        if (_price > 0) {
          mainButton.setParams({
            is_visible: true,
            text: `Оплатить · ${_price}₽`,
            color: '#40C62B'
          }).hideProgress();
        } else {
          mainButton.setParams({
            is_visible: false,
            text: `Оплатить · ${_price}₽`,
            color: '#40C62B'
          }).hideProgress();
        }
      }
    }
      var mainButton = window.Telegram.WebApp.MainButton;
      mainButton.setParams({
        is_visible: home.cart.length > 0 ? true : false,
        text: `Оплатить · ${home.price}₽`,
        color: '#40C62B'
      }).hideProgress();
    };

    return (
        <div>
            <Modal opened={home.activeModal == 'deliveryOrder'}>
              <div className='product-image'>
                <img className='odnaZalupa' src={close} onClick={() => {
                  dispatch({
                    type: "updateModal",
                    payload: null
                  })
                  var mainButton = window.Telegram.WebApp.MainButton;
                  mainButton.setParams({
                    is_visible: true,
                    text: `Оплатить · ${home.price}₽`,
                    color: '#40C62B'
                  }).hideProgress();
                }} />
              </div>
              <div className='pay'>
                <div className='title'>Оформление доставки</div>
                <div className='desc'>Счет #21452551</div>

                <div className='cart'>
                  {home.cart.map((data, i) => {
                    return (
                      <div className='cell'>
                        <img src={data.data.img} />
                        <div className='label'>{data.data.title} <p>{data.count}шт.</p></div>
                        <div className='amount'>{Number(data.data.price) * Number(data.count)}{data.data.currency}</div>
                      </div>
                    )
                  })}
                </div>
                <Input value={home.deliveryData.phone} onChange={(e) => {
                  let val = e.target.value.replace(/\D/g, '').match(/(\d{0,1})(\d{0,3})(\d{0,3})(\d{0,4})/);
              
                  let num = `+${val[1]}${val[2] ? ` ${val[2]}` : ""}${val[3] ? ` ${val[3]}` : ""}${val[4] ? ` ${val[4]}` : ""}`;
              
              
                  dispatch({ "type": "updateDeliveryData", payload: {...home.deliveryData, phone: num} })
                
                }} type="tel" icon={promo} placeholder="Номер телефона" />
                
                <Input refElement={dropdown} value={home.deliveryData.address} onFocus={() => setShown(true)} onBlur={() => {
                  setTimeout(() => {
                    setShown(false)
                  }, 1000)
                }} onChange={async (e) => {
                  dispatch({ "type": "updateDeliveryData", payload: {...home.deliveryData, address: e.target.value} })
                  
                }} icon={location} placeholder="Адрес" />
          
                <Dropdown shown={shown} count={home.cart.length} refElement={dropdown} dropdownWidth={`${Number(352 - 48)}px`}>
                  {home.addressSuggestions.length < 1 && <div className='dropdownSelect'>Начните вводить адрес, чтобы получить подсказки</div>}
                  {home.addressSuggestions.map((info, i) => {
                    return (
                      <div className='dropdownSelect' onClick={() => {
                        dispatch({ "type": "updateDeliveryData", payload: {...home.deliveryData, address: info.value} })
                        setShown(false)
                      }}>
                        <img src={location} />
                        {info.value}
                      </div>
                    )
                  })}
                </Dropdown>
                <div className='mainButton' onClick={async () => {
                  const api = new API({
                    id: Number(window.rid),
                    location: Number(window.lid)
                  })
                  let req = await api.orderDelivery({
                    id: new URLSearchParams(window.Telegram.WebApp.initData).get('query_id'),
                    cart: home.cart,
                    deliveryData: home.deliveryData,
                    rid: api.id
                  })
                  if (req.status != 'ok') {
                    dispatch({
                      type: "updateRequestError",
                      payload: req.error
                    })
                  } else {
                    window.Telegram.WebApp.close()
                  }
                }}>Оплатить · {home.price}₽</div>
                <div className='secondaryButton' onClick={() => {
                  dispatch({
                    type: "updateModal",
                    payload: 'order'
                  })
                }}>Купить в ресторане</div>
              </div>
            </Modal>
            <Modal opened={home.activeModal == 'order'}>
              <div className='product-image'>
                <img className='odnaZalupa' src={close} onClick={() => {
                  dispatch({
                    type: "updateModal",
                    payload: null
                  })
                  var mainButton = window.Telegram.WebApp.MainButton;
                  mainButton.setParams({
                    is_visible: true,
                    text: `Оплатить · ${home.price}₽`,
                    color: '#40C62B'
                  }).hideProgress();
                }} />
              </div>
              <div className='pay'>
                <div className='title'>Оплатить</div>
                <div className='desc'>Счет #21452551</div>

                <div className='cart'>
                  {home.cart.map((data, i) => {
                    return (
                      <div className='cell'>
                        <img src={data.data.img} />
                        <div className='label'>{data.data.title} <p>{data.count}шт.</p></div>
                        <div className='amount'>{Number(data.data.price) * Number(data.count)}{data.data.currency}</div>
                      </div>
                    )
                  })}
                </div>
                <Input icon={promo} placeholder="Промокод" />
                <div className='mainButton' onClick={async () => {
                  const api = new API({
                    id: Number(window.rid),
                    location: Number(window.lid)
                  })
                  await api.order({
                    id: new URLSearchParams(window.Telegram.WebApp.initData).get('query_id'),
                    cart: home.cart,
                    rid: api.id
                  })
                  window.Telegram.WebApp.close()
                  
                }}>Оплатить · {home.price}₽</div>
                <div className='secondaryButton' onClick={() => {
                  dispatch({
                    type: "updateModal",
                    payload: 'deliveryOrder'
                  })
                }}>Оформить доставку</div>
              </div>
            </Modal>
            <Modal opened={home.activeModal == 'productInfo'}>
              <div className='product-image'>
                <img className='odnaZalupa' src={close} onClick={() => {
                  dispatch({
                    type: "updateModal",
                    payload: null
                  })
                }} />
                <img className='vtorayaZalupa' src={home.selectedProduct.img} />
              </div>
              <div className='product-info'>
                <div className='title'>{home.selectedProduct.title}</div>
                <div className='desc'>{home.selectedProduct.info}</div>
              </div>
              <div className='productDescription'>{home.selectedProduct.description}</div>
              <ul>
                {home.selectedProduct.structure.map((info, i) => {
                  return (
                    <li>{info.label}</li>
                  )
                })}
              </ul>
              <div className='nutritionals'>
                {home.selectedProduct.nutritional.map((info, i) => {
                  return (
                    <div className='nutritional'>
                      <div className='title'>{info.title}</div>
                      <div className='value'>{info.value}</div>
                    </div>
                  )
                })}
              </div>
              <div className='buttons'>
                <div className='button1'>{home.selectedProduct.price}{home.selectedProduct.currency} · {home.selectedProduct.info}</div>
                {!home.cart.find(item => home.selectedProduct.id === item.id) ? (
                  <div className='button2' onClick={() => addToCart(home.selectedProduct)}>Добавить</div>
                ) : (
                <div className='info'>
                  <div className='cart'>
                    <div className='unadd' onClick={() => deleteFromCart(home.selectedProduct)}><img src={minus} /></div>
                    <div className='count'>{(home.cart.find(item => home.selectedProduct.id === item.id)).count}</div>
                    <div className='add' onClick={() => addToCart(home.selectedProduct)}><img src={plus} /></div>
                  </div>
                </div>
                )}
              </div>
            </Modal>
            {home.menu.map((category, i) => {
                return (
                  <ProductsGroup cid={category.id} location={window.lid} deleteFromCart={deleteFromCart} setActiveModal={(e) => dispatch({ type: "updateModal", payload: e })} setSelectedProduct={(e) => dispatch({ type: "updateSelectedProduct", payload: e })} addToCart={addToCart} cart={home.cart} title={category.title} items={category.products} />
                )
            })}
            <Snackbar shown={home.requestError != null} error={true} onClose={() => dispatch({ type: "updateRequestError", payload: null })}>{home.requestError}</Snackbar>
            <ScrollTop />
          </div>
            
    )
}

export default HomeView;