import React, { Component } from "react";
import { BrowserRouter, Route } from "react-router-dom";
import ErrorView from "./views/Error";

import Home from "./views/Home";
import QrReaderView from "./views/QrReader";

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activePanel: 'qr',
      error: "",
      id: ''
    };
  }

  goToHome = (decodedText) => {
    this.setState({ id: decodedText })
    this.setState({ activePanel: 'home' })
  }

  componentDidMount() {
    if (window.location.href.split('?')[1]) {
      let a1 = window.location.href.split('?')[1].split('#')
      let a2 = a1[0].split('&')
      let a3 = a2[0].split('=')
      let a4 = a2[1].split('=')
          
      if (a3[1] && a4[1]) {
        this.setState({ activePanel: 'home' })
      }
    }
  }

  componentDidCatch(error, info) {
    console.log('error')
    console.log(error)
    
    this.setState({error: error})
  }

  render() {
    return (
      <>
        {!this.state.error ? <div>
        {this.state.activePanel == 'home' ?
          <Home id={this.state.id} />
        : 
        this.state.activePanel == 'qr' ? 
          <QrReaderView goToHome={this.goToHome} /> 
        : 
        <></>}
      </div> : <ErrorView error={this.state.error} />}
      </>
    );
  }
}

export default App;