import React, { useEffect, useState, useRef } from 'react';
import { motion } from 'framer-motion'

import qr from '../assets/qr.svg'

import '../index.css'

const QrReaderView = props => {
  const [devices, setDevices] = useState({})
  const [error, setError] = useState('')
  const [active, setActive] = useState('confirm')

  useEffect(() => {
    window.Telegram.WebApp.expand()
  }, [])

  return (
    <>
      <div id="qr-reader" style={{ width: active == 'scan' && '100%', height: active == 'scan' && "100%"}}></div>
        {active == 'confirm' && <div className='scanner'>
            <img src={qr} />
            <div className='scanner-text'>Ресторан не выбран</div>
            <div className='scanner-button' onClick={() => {
                setActive('scan')
                    var html5QrcodeScanner = new window.Html5Qrcode("qr-reader");
                    html5QrcodeScanner.start(
                      { facingMode: "environment" }, 
                        {
                        fps: 20,
                        qrbox: { width: 250, height: 250 }
                        },
                        (decodedText, decodedResult) => {
                            decodedText = decodedText.replace('https://t.me/tg_foodbot?start=', '')
                            window.rid = decodedText.split('_')[0]
                            window.lid = decodedText.split('_')[1]
                            props.goToHome(decodedText)
                        },
                        (errorMessage) => {
                          setError(errorMessage)
                        })
                    .catch((err) => {
                      setError(err)
                    });
        }}>Отсканировать QR-код ресторана</div>
        </div>}
    </>
  );
}

export default QrReaderView;