class API {
    constructor(data) {
        this.id = data.id;
        this.location = data.location;
        this.apiEndpoint = 'https://9bcd-2-132-236-137.eu.ngrok.io/api/'
    }

    async callGet(route, query) {
        try {
            let req = await fetch(`${this.apiEndpoint}${route}${query}`)
            req = await req.json()

            return req
        } catch(e) {
            window.hawk.send(new Error(`GET request to ${route} failed`), {
                error: e.toString(),
                apiEndpoint: this.apiEndpoint,
                userParams: JSON.stringify({
                    id: JSON.parse(new URLSearchParams(window.Telegram.WebApp.initData).get('user')).id
                })
            });
        }
    }

    async callPost(route, body) {
        try {
            let req = await fetch(`${this.apiEndpoint}${route}`, {
                method: 'POST',
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    ...body,
                    tgdata: window.Telegram.WebApp.initData
                })
            })
            req = await req.json()

            return req
        } catch(e) {
            window.hawk.send(new Error(`POST request to ${route} failed`), {
                error: e.toString(),
                apiEndpoint: this.apiEndpoint,
                userParams: JSON.stringify({
                    id: JSON.parse(new URLSearchParams(window.Telegram.WebApp.initData).get('user')).id
                })
            });
        }
    }

    async getInfo() {
        let res = await this.callGet('restaurants/getInfo', `?id=${this.id}&location=${this.location}`)

        return res
    }

    async order(data) {
        let res = await this.callPost('order/create', data)

        return res
    }

    async orderDelivery(data) {
        let res = await this.callPost('order/delivery/create', data)

        return res
    }

    async suggestAddress(query) {
        let res = await this.callGet('suggest/address', `?query=${query}`)

        return res
    }
}

export default API